import React, { Component } from 'react'

export class UpdateRecord extends Component {
    constructor(props) {
        super(props);
        this.state = {
            serialNo:'',
            macId: '',
            testerName:'',
            date:'',
            visualInspection: '',
            inverseResistance:'',
            directResistance:'',
            batteryVoltage:''
        };
    }

    handleVisualInspectionChange = (event) => {
        // console.log("Test");
        this.setState({visualInspection: !this.state.visualInspection});
    }
    
    handleTesterNameChange = (event) => {
        this.setState({testerName: event.target.value});
    }

    handleSerialNoChange = (event) => {
        this.setState({serialNo: event.target.value});
    }

    handleMacIdChange = (event) => {
        this.setState({macId: event.target.value});
    }
    handleInverseResistanceChange = (event) => {
        this.setState({inverseResistance: event.target.value});
    }

    handleDirectResistanceChange = (event) => {
        this.setState({directResistance: event.target.value});
    }

    handleBatteryVoltageChange = (event) => {
        this.setState({batteryVoltage: event.target.value});
    }
    
    handleNoteChange = (event) => {
        this.setState({note: event.target.value});
    }
    
    handleSearch = (event) => {
        // send get request to search record in the DB
        event.preventDefault();
        const { serialNo, macId, testerName } = this.state;
        var loggingTime = new Date();
        var date;
        date = loggingTime.getDate();
        console.log("Date = " + date);
        console.log("Serial No = " + serialNo + " MAC ID = " + macId );
        console.log("Tester Name = " + testerName);
    }

    handleUpdate = (event) => {
        // feel in the new params and post the record to the DB
        event.preventDefault();
        const { visualInspection, directResistance, inverseResistance, batteryVoltage,  note } = this.state;
        var loggingTime = new Date();
        var date;
        date = loggingTime.getDate();
        console.log("Date = " + date);
        console.log("Visual Inspection = " + visualInspection );
        console.log("Inverse Resistance = " + inverseResistance);
        console.log("Direct Resistance = " + directResistance);
        console.log("Battery Voltage = " + batteryVoltage);
        console.log("Note = " + note);
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSearch}>

                    <div className="form-group">
                        <p>              
                            <label htmlFor="testerName"> Tester Name :  </label>
                            <select name="testerName" className="form-control" id="testerName" onChange={this.handleTesterNameChange}>
                                <option value="Unknown">Select Name</option>   
                                <option value="Phil Walden">Phil</option>
                                <option value="Sam Baskin">Sam</option>
                                <option value="Nick Crocker">Nick</option>
                                <option value="Carlos Martins">Carlos</option>
                                <option value="Melissa Cross">Melissa</option>
                                <option value="Dhaval Chauhan">Dhaval</option>
                            </select>
                        </p>
                   
                        <p>
                            <label htmlFor="serialNo"> Search by Serial No :  </label>
                            <input name="serialNo" className="form-control" id="serialNo" value={this.state.serialNo} onChange={this.handleSerialNoChange} />

                            <label htmlFor="macId"> Search by MAC ID : </label>
                            <input name="macId" className="form-control" id="macId" value={this.state.macId} onChange={this.handleMacIdChange} />
                        </p>

                    </div>

                    <p align="center">
                        <button type="submit" className="btn btn-success btn-block">Search</button>
                    </p>
                    
                </form>


                <form onSubmit={this.handleUpdate}>
                    <div className="form-group">
                        <h2>Enter New Values After Freezer Test</h2>
                        <p>
                            <label htmlFor="visualInspection"> 1. Visual Inspection :  </label>
                            <input type="checkbox" className="checkbox-control" onChange={this.handleVisualInspectionChange} name="visualInspection"/>
                        </p>

                        <p>
                            <label htmlFor="inverseResistance"> 2. Inverse Resistance : </label>
                            <input name="inverseResistance" className="form-control" id="inverseResistance" value={this.state.inverseResistance} onChange={this.handleInverseResistanceChange} />
                        </p>

                        <p>
                            <label htmlFor="directResistance"> 3. Direct Resistance : </label>
                            <input name="directResistance" className="form-control" id="directResistance" value={this.state.directResistance} onChange={this.handleDirectResistanceChange} />
                        </p>

                        <p>
                            <label htmlFor="batteryVoltage"> 4. Battery Voltage : </label>
                            <input name="batteryVoltage" className="form-control" id="batteryVoltage" value={this.state.batteryVoltage} onChange={this.handleBatteryVoltageChange} />
                        </p>

                        <p>
                            <label htmlFor="note"> 5. Note : </label>
                            <br></br>
                            <textarea name="note" className="note-control" id="note" value={this.state.note} onChange={this.handleNoteChange} rows="5" cols="100"></textarea>
                        </p>
                        
                    </div>

                    <p align="center">
                        <button type="submit" className="btn btn-success btn-block">Update</button>
                    </p>
                    
                </form>

            </div>
        )
    }
}

export default UpdateRecord
